module.exports = "class ControlledPanelGroup extends React.Component {\r\n  constructor(props, context) {\r\n    super(props, context);\r\n\r\n    this.handleSelect = this.handleSelect.bind(this);\r\n\r\n    this.state = {\r\n      activeKey: '1'\r\n    };\r\n  }\r\n\r\n  handleSelect(activeKey) {\r\n    this.setState({ activeKey });\r\n  }\r\n\r\n  render() {\r\n    return (\r\n      <PanelGroup\r\n        accordion\r\n        id=\"accordion-controlled-example\"\r\n        activeKey={this.state.activeKey}\r\n        onSelect={this.handleSelect}\r\n      >\r\n        <Panel eventKey=\"1\">\r\n          <Panel.Heading>\r\n            <Panel.Title toggle>Panel heading 1</Panel.Title>\r\n          </Panel.Heading>\r\n          <Panel.Body collapsible>Panel content 1</Panel.Body>\r\n        </Panel>\r\n        <Panel eventKey=\"2\">\r\n          <Panel.Heading>\r\n            <Panel.Title toggle>Panel heading 2</Panel.Title>\r\n          </Panel.Heading>\r\n          <Panel.Body collapsible>Panel content 2</Panel.Body>\r\n        </Panel>\r\n      </PanelGroup>\r\n    );\r\n  }\r\n}\r\n\r\nrender(<ControlledPanelGroup />);\r\n"


