module.exports = "class Example extends React.Component {\r\n  constructor(props, context) {\r\n    super(props, context);\r\n\r\n    this.state = {\r\n      open: true\r\n    };\r\n  }\r\n\r\n  render() {\r\n    return (\r\n      <div>\r\n        <Button onClick={() => this.setState({ open: !this.state.open })}>\r\n          Click to toggle\r\n        </Button>\r\n        <br />\r\n        <Panel id=\"collapsible-panel-example-1\" expanded={this.state.open}>\r\n          <Panel.Collapse>\r\n            <Panel.Body>\r\n              Anim pariatur cliche reprehenderit, enim eiusmod high life\r\n              accusamus terry richardson ad squid. Nihil anim keffiyeh\r\n              helvetica, craft beer labore wes anderson cred nesciunt sapiente\r\n              ea proident.\r\n            </Panel.Body>\r\n          </Panel.Collapse>\r\n        </Panel>\r\n\r\n        <p>You can also make the Panel heading toggle the collapse.</p>\r\n\r\n        <Panel id=\"collapsible-panel-example-2\" defaultExpanded>\r\n          <Panel.Heading>\r\n            <Panel.Title toggle>\r\n              Title that functions as a collapse toggle\r\n            </Panel.Title>\r\n          </Panel.Heading>\r\n          <Panel.Collapse>\r\n            <Panel.Body>\r\n              Anim pariatur cliche reprehenderit, enim eiusmod high life\r\n              accusamus terry richardson ad squid. Nihil anim keffiyeh\r\n              helvetica, craft beer labore wes anderson cred nesciunt sapiente\r\n              ea proident.\r\n            </Panel.Body>\r\n          </Panel.Collapse>\r\n        </Panel>\r\n\r\n        <p>Or use a Panel.Toggle component to customize</p>\r\n\r\n        <Panel id=\"collapsible-panel-example-3\" defaultExpanded>\r\n          <Panel.Heading>\r\n            <Panel.Title>Title that functions as a collapse toggle</Panel.Title>\r\n            <Panel.Toggle componentClass=\"a\">My own toggle</Panel.Toggle>\r\n          </Panel.Heading>\r\n          <Panel.Collapse>\r\n            <Panel.Body>\r\n              Anim pariatur cliche reprehenderit, enim eiusmod high life\r\n              accusamus terry richardson ad squid. Nihil anim keffiyeh\r\n              helvetica, craft beer labore wes anderson cred nesciunt sapiente\r\n              ea proident.\r\n            </Panel.Body>\r\n          </Panel.Collapse>\r\n        </Panel>\r\n      </div>\r\n    );\r\n  }\r\n}\r\n\r\nrender(<Example />);\r\n"


