module.exports = "<div>\r\n  <Panel>\r\n    <Panel.Heading>Panel heading without a title</Panel.Heading>\r\n    <Panel.Body>Panel content</Panel.Body>\r\n  </Panel>\r\n  <Panel>\r\n    <Panel.Heading>\r\n      <Panel.Title componentClass=\"h3\">Panel heading with a title</Panel.Title>\r\n    </Panel.Heading>\r\n    <Panel.Body>Panel content</Panel.Body>\r\n  </Panel>\r\n</div>;\r\n"


